import { apiOptions } from './constant';

class Api {
  constructor(apiOptions) {
    this._url = apiOptions.baseUrl;
    this._headers = apiOptions.headers;
    this._credentials = apiOptions.credentials;
  }

 //–û—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å
 _sendRequest(path, parameters) {
  return fetch(`${this._url}${path}`, parameters).then((res) => {
    if (res.ok) {
      return res.json();
    }
    return Promise.reject(res.status);
  });
}

  //–ü–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  getUserInfo() {
    return this._sendRequest(`users/me`, {
      headers: this._headers,
      credentials: this._credentials,
    });
  }

   //–ü–æ–ª—É—á–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫–∏
   getInitialCards() {
    return this._sendRequest(`cards`, {
      headers: this._headers,
      credentials: this._credentials,
    });
  }

  //–û–±–Ω–æ–≤–∏—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
  //–û–±–Ω–æ–≤–∏—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
  updateUserInfo(newUserInfo) {
    return this._sendRequest(`users/me`, {
      method: 'PATCH',
      headers: this._headers,
      credentials: this._credentials,
      body: JSON.stringify({
        name: newUserInfo.name,
        about: newUserInfo.about,
      }),
    });
  }

  //–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
  postNewCard(newCard) {
    return this._sendRequest(`cards`, {
      method: 'POST',
      body: JSON.stringify({
        name: newCard.title,
        link: newCard.link,
      }),
      headers: this._headers,
      credentials: this._credentials,
    });
  }

  changeLikeCardStatus(id, isLiked) {
    return this._sendRequest(`cards/likes/${id}`, {
      method: `${isLiked ? 'PUT' : 'DELETE'}`,
      headers: this._headers,
      credentials: this._credentials,
    });
  }

  //–£–¥–∞–ª–∏—Ç—å —Ñ–æ—Ç–æ
  deleteCard(id) {
    return this._sendRequest(`cards/${id}`, {
      method: 'DELETE',
      headers: this._headers,
      credentials: this._credentials,
    });
  }


  //–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä
  updateUserAvatar(avatar) {
    return this._sendRequest(`users/me/avatar`, {
      method: 'PATCH',
      body: JSON.stringify({ avatar: avatar.url }),
      headers: this._headers,
      credentials: this._credentials,
    });
  }
}

export const api = new Api(apiOptions);