import BadRequestError from '../errors/badRequestError';
import UnauthorizedError from '../errors/unauthorizedError';


export const BASE_URL = 'https://api.advent.students.nomoredomains.rocks';


// –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –∑–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é
export const register = (password, email) => fetch(`${BASE_URL}/signup`, {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  —Åredentials: 'include',
  body: JSON.stringify({ password, email }),
})
  .then((res) => {
    if (!res.ok) {
      return res.json()
        .then((err) => {
          if (err.error) {
            throw new BadRequestError(err.error);
          } else {
            throw new BadRequestError(err.message);
          }
        });
    }
    return res.json();
});

// –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –∑–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é
export const authorize = (password, email) => fetch(`${BASE_URL}/signin`, {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  —Åredentials: 'include',
  body: JSON.stringify({ password, email }),
})
  .then((res) => {
    if (res.status === 400) {
      throw new BadRequestError('–ù–µ –ø–µ—Ä–µ–¥–∞–Ω–æ –æ–¥–Ω–æ –∏–∑ –ø–æ–ª–µ–π');
    }
    else if (res.status === 401) {
      throw new UnauthorizedError('–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å —Ç–∞–∫–∏–º email –Ω–µ –Ω–∞–π–¥–µ–Ω');
    }
    return res.json();
});

