
import React, { useState }  from 'react';
import {
  Route, Switch, useLocation, Redirect, useHistory,
} from 'react-router-dom';

import '../index.css';
import { api } from '../utils/Api.js';
import Header from '../components/Header.js';
import Main from '../components/Main.js';
import Footer from '../components/Footer.js';
import ImagePopup from '../components/ImagePopup.js';
import EditAvatarPopup from '../components/EditAvatarPopup.js';
import EditProfilePopup from '../components/EditProfilePopup.js';
import AddPlacePopup from '../components/AddPlacePopup.js';
import ConfirmPopup from './ConfirmPopup.js';
import ProtectedRoute from './ProtectedRoute';
import InfoTooltip from './InfoTooltip';
import * as auth from '../utils/auth';
import { CurrentUserContext }  from '../contexts/CurrentUserContext.js'
import Login from './Login';
import Register from './Register';
import resolvePath from '../images/resolve.svg';
import rejectPath from '../images/reject.svg';
import loading from '../images/loading.svg';


function App() {

  const [isEditProfilePopupOpen, setEditProfilePopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setAddPlacePopupOpen] = useState(false);
  const [isEditAvatarPopupOpen, setEditAvatarPopupOpen] = useState(false);
  const [isConfirmPopupOpen, setConfirmPopupOpen] = useState(false);
  const [cards, setCards] = useState([]);
  const [selectedCard, setSelectedCard] = useState({
    isImageOpen: false,
    link: '',
    name: '',
  });

  const [currentUser, setCurrentUser] = useState({})
  const [cardToDelete, setCardToDelete] = useState({});
  const [isLoading, setLoading] = useState(false);

  // –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è
  const [loggedIn, setLoggedIn] = useState(false);
  const [email, setEmail] = useState('');
  const [isAuthInfoOpened, setAuthInfoOpened] = useState(false);
  const [isInfoTooltipOpen, setInfoTooltipOpen] = useState(false);
  const [message, setMessage] = useState({
    iconPath: loading,
    text: ''
  });
  const location = useLocation();
  const history = useHistory();

  const escape = require('escape-html');



  // –ü—Ä–æ–≤–µ—Ä–∏—Ç—å —Ç–æ–∫–µ–Ω. –Ω–∞–º –±–æ–ª—å—à–µ –Ω–µ –Ω–∞–¥–æ —Ç–∞–∫ –∫–∞–∫ —Ç–æ–∫–µ–Ω –ø—Ä–æ–≤–µ—Ä—è—é—Ç –∫—É–∫–∏
  // React.useEffect(() => {
  //   const jwt = localStorage.getItem('jwt');
  //   if (jwt) {
  //     api.getContent(jwt)
  //       .then((res) => {
  //         setLoggedIn(true);
  //         setEmail(res.data.email);
  //         history.push('/');
  //       })
  //       .catch(err => console.log(err));
  //   }
  // }, [history]);


// –ø–æ—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
React.useEffect(() => {
  if (loggedIn) {
    api
      .getUserInfo()
      .then((res) => {
        console.log(res)
        setCurrentUser(res);
        console.log(currentUser)
      })
      .catch((err) => console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ: ${err}`));
    }
}, [loggedIn]);

// –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
function handleRegister(password, email) {
  auth.register(escape(password), email)
    .then(() => {
      setMessage({ iconPath: resolvePath, text: '–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!' });
      history.push('/signin');
    })
    .catch((err) => setMessage({ iconPath: rejectPath, text: err.message }));
  setInfoTooltipOpen(true);
}

// –ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è
function handleLogin(password, email) {
  auth.authorize(escape(password), email)
    .then((data) => {
      setLoggedIn(true);
      setMessage({ iconPath: resolvePath, text: '–í—ã —É—Å–ø–µ—à–Ω–æ –≤–æ—à–ª–∏ –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ!' });
      history.push('/');
    })
    .catch((err) => setMessage({ iconPath: rejectPath, text: err.message }))
  setInfoTooltipOpen(true);
}


  // –í—ã—Ö–æ–¥
  function handleSignOut() {
    setLoggedIn(false);
    localStorage.removeItem('jwt');
    setEmail('');
    history.push('/signin');
  }

  // –ü–æ–ª—É—á–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫–∏
  React.useEffect(() => {
    if (loggedIn) {
      api
        .getInitialCards()
        .then((cardData) => {
          setCards(cardData);
        })
        .catch((err) => console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∫–∞—Ä—Ç–æ—á–µ–∫: ${err}`));
      }
  }, [loggedIn]);



  //–û—Ç–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –∞–≤–∞—Ç–∞—Ä–∞
  function handleEditAvatarClick() {
    setEditAvatarPopupOpen(true);
  }
  //–û—Ç–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –ø—Ä–æ—Ñ–∏–∏–ª—è
  function handleEditProfileClick() {
    setEditProfilePopupOpen(true);
  }
  //–û—Ç–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –∫–∞—Ä—Ç–æ—á–∫–∏
  function handleAddPlaceClick() {
    setAddPlacePopupOpen(true);
  }
  // –æ—Ç–∫—Ä—ã—Ç—å —Ñ—É–ª—Å–∞–π–∑ —Ñ–æ—Ç–æ
  function handleCardClick(card) {
    const { link, name } = card;
    setSelectedCard({ isImageOpen: true, link: link, name: name });
  }
  function handleCardLike(card) {
    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    api
      .changeLikeCardStatus(card._id, !isLiked)
      .then((newCard) => {
        // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
      const newCards = cards.map((c) => c._id === card._id ? newCard : c);
      // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
      setCards(newCards);
    })
    .catch((err) =>
        console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ—Å—Ç–∞–Ω–æ–≤–∫–µ –ª–∞–π–∫–∞: ${err}`)
    );
  } 
   //–£–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –ø–æ—Å–ª–µ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
   function handleConfirm() {
    api
      .deleteCard(cardToDelete._id)
      .then(() =>{
        setCards(cards.filter((item) => item !== cardToDelete))
        closeAllPopups();
      })
      .catch((err) => console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –∫–∞—Ä—Ç–æ—á–∫–∏: ${err}`));
    
  }
  //–ö–ª–∏–∫–Ω—É—Ç—å –Ω–∞ —É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
  function handleCardDelete(card) {
    setConfirmPopupOpen(true);
    setCardToDelete(card);
  }
  //–ó–∞–∫—Ä—ã—Ç—å –≤—Å–µ –ø–æ–ø–∞–ø—ã
  function closeAllPopups() {
    setEditProfilePopupOpen(false);
    setEditAvatarPopupOpen(false);
    setAddPlacePopupOpen(false);
    setSelectedCard({});
    setConfirmPopupOpen(false);
    setInfoTooltipOpen(false)
  }
  //–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä
  function handleUpdateAvatar(newAvatar) {
      setLoading(true);
      api
        .setUserAvatar(newAvatar)
        .then((res) => {
          setCurrentUser(res);
          closeAllPopups();
        })
        .catch((err) => console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –∞–≤–∞—Ç–∞—Ä–∞: ${err}`))
        .finally(() => setLoading(false));
        
  }
  //–û–±–Ω–æ–≤–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  function handleUpdateUser(userData) {
      setLoading(true);
      api
        .updateUserInfo(userData)
        .then((newUser) => {
          setCurrentUser(newUser);
          closeAllPopups();
        })
        .catch((err) => `–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ: ${err}`)
        .finally(() => setLoading(false));
      
  }
  
  function handleAddPlace(card) {
      setLoading(true);
      api
        .postNewCard(card)
        .then((newCard) => {
          setCards([newCard, ...cards]);
          closeAllPopups();
        })
        .catch((err) =>
          console.log(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏: ${err}`)
        )
        .finally(() => setLoading(false));
      
  }
    
  return (
    <CurrentUserContext.Provider value={currentUser}>
        <div className="root">
          <div className="page">
          {(loggedIn && isAuthInfoOpened)}

            <Header loggedIn={loggedIn}
                    locaction={location}
                    email={email}
                    signOut={handleSignOut}
                    isAuthInfoOpened={isAuthInfoOpened}
            />
            <Switch>
      
            <ProtectedRoute exact path="/" loggedIn={loggedIn} component={Main}
                    cards={cards}
                    onCardClick={handleCardClick}
                    onCardLike={handleCardLike}
                    onCardDelete={handleCardDelete}
                    onEditProfile={handleEditProfileClick}
                    onAddPlace={handleAddPlaceClick}
                    onEditAvatar={handleEditAvatarClick}
            />
          <Route path='/signin'>
            <Login onLogin={handleLogin} />
          </Route>
         
          <Route path='/signup'>
            <Register onRegister={handleRegister} />
          </Route>
          <Route>
            {loggedIn ? <Redirect to="/" /> : <Redirect to="/signin" />}
          </Route>
        </Switch>

        <Footer />

        <InfoTooltip
          isOpen={isInfoTooltipOpen}
          onClose={closeAllPopups}
          loggedIn={loggedIn}
          message={message}
        />

            <EditProfilePopup
              isOpen={isEditProfilePopupOpen}
              onClose={closeAllPopups}
              onUpdateUser={handleUpdateUser}
              isLoading={isLoading}
            />
            <EditAvatarPopup
              onUpdateAvatar={handleUpdateAvatar}
              isOpen={isEditAvatarPopupOpen}
              onClose={closeAllPopups}
              isLoading={isLoading}
            />
            <AddPlacePopup
              isOpen={isAddPlacePopupOpen}
              onClose={closeAllPopups}
              onAddPlace={handleAddPlace}
            />
            <ConfirmPopup
              isOpen={isConfirmPopupOpen}
              onClose={closeAllPopups}
              onConfirmDelete={handleConfirm}
            />
          
            <ImagePopup 
            name={selectedCard.name}
            link={selectedCard.link}
            isOpen={selectedCard.isImageOpen}
            onClose={closeAllPopups}
            />
            
          </div>
        </div>
    </CurrentUserContext.Provider>
  );
}


export default App;
